import { Basis } from '../../../basis';

const SIZES = ['sm', 'md', 'lg'];

class Logo extends Basis {
  constructor (data = {
    id : 'logo',
    classes : [],
    attributes : {},
    size: 'md',
    title: 'République<br>Française',
  }) {
    super(data);
    this._size = data.size;
    this._title = data.title;
  }

  get size () {
    return this._size;
  }

  set size (value) {
    if (!SIZES.includes(value)) return;
    this._size = value;
  }

  get title () {
    return this._title;
  }

  set title (value) {
    this._title = value;
  }

  get classes () {
    const classes = [
      'fr-logo',
      ... super.classes
    ];

    if (this._size !== 'md') classes.push(`fr-logo--${this._size}`);

    return classes;
  }

  get html () {
    return `<p ${this.includeClasses()} ${this.includeAttributes()}>${this._title}</p>`;
  }
}

export { Logo };
